import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '@/store'
import { getToken } from '@/utils/auth'

// 1.ÂàõÂª∫axiosÂÆû‰æã
const service = axios.create({
  // ÂÖ¨ÂÖ±Êé•Âè£--ËøôÈáåÊ≥®ÊÑèÂêéÈù¢‰ºöËÆ≤,url = base url + request url
  baseURL: process.env.VUE_APP_SERVER_URL,

  // baseURL: 'https://api.example.com',
  // Ë∂ÖÊó∂Êó∂Èó¥ Âçï‰ΩçÊòØmsÔºåËøôÈáåËÆæÁΩÆ‰∫Ü5sÁöÑË∂ÖÊó∂Êó∂Èó¥
  timeout: 5 * 1000
})

// 2.ËØ∑Ê±ÇÊã¶Êà™Âô®request interceptor
service.interceptors.request.use(
  config => {
    // ÂèëËØ∑Ê±ÇÂâçÂÅöÁöÑ‰∏Ä‰∫õÂ§ÑÁêÜÔºåÊï∞ÊçÆËΩ¨ÂåñÔºåÈÖçÁΩÆËØ∑Ê±ÇÂ§¥ÔºåËÆæÁΩÆtoken,ËÆæÁΩÆloadingÁ≠âÔºåÊ†πÊçÆÈúÄÊ±ÇÂéªÊ∑ªÂä†
    // Ê≥®ÊÑè‰ΩøÁî®tokenÁöÑÊó∂ÂÄôÈúÄË¶ÅÂºïÂÖ•cookieÊñπÊ≥ïÊàñËÄÖÁî®Êú¨Âú∞localStorageÁ≠âÊñπÊ≥ïÔºåÊé®Ëçêjs-cookie
    if (store.getters.token) {
      // config.params = {'token': token}    // Â¶ÇÊûúË¶ÅÊ±ÇÊê∫Â∏¶Âú®ÂèÇÊï∞‰∏≠
      // config.headers.token = token;       // Â¶ÇÊûúË¶ÅÊ±ÇÊê∫Â∏¶Âú®ËØ∑Ê±ÇÂ§¥‰∏≠
      // bearerÔºöw3cËßÑËåÉ
      config.headers['Authorization'] = 'Bearer ' + getToken()
    }
    return config
  },
  error => {
    // do something with request error
    // console.log(error) // for debug
    return Promise.reject(error)
  }
)


// ËÆæÁΩÆcrossË∑®Âüü Âπ∂ËÆæÁΩÆËÆøÈóÆÊùÉÈôê ÂÖÅËÆ∏Ë∑®ÂüüÊê∫Â∏¶cookie‰ø°ÊÅØ,‰ΩøÁî®JWTÂèØÂÖ≥Èó≠
service.defaults.withCredentials = false

service.interceptors.response.use(
  // Êé•Êî∂Âà∞ÂìçÂ∫îÊï∞ÊçÆÂπ∂ÊàêÂäüÂêéÁöÑ‰∏Ä‰∫õÂÖ±ÊúâÁöÑÂ§ÑÁêÜÔºåÂÖ≥Èó≠loadingÁ≠â
  response => {
    const res = response.data
    // Â¶ÇÊûúËá™ÂÆö‰πâ‰ª£Á†Å‰∏çÊòØ200ÔºåÂàôÂ∞ÜÂÖ∂Âà§Êñ≠‰∏∫ÈîôËØØ„ÄÇ
    if (res.code !== 200) {
      // 50008: ÈùûÊ≥ïToken; 50012: ÂºÇÂú∞ÁôªÂΩï; 50014: TokenÂ§±Êïà;
      if (res.code === 401 || res.code === 50012 || res.code === 50014) {
        // ÈáçÊñ∞ÁôªÂΩï
        MessageBox.confirm('‰ºöËØùÂ§±ÊïàÔºåÊÇ®ÂèØ‰ª•ÁïôÂú®ÂΩìÂâçÈ°µÈù¢ÔºåÊàñÈáçÊñ∞ÁôªÂΩï', 'ÊùÉÈôê‰∏çË∂≥', {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning',
          center: true
        }).then(() => {
          window.location.href = '#/login'
        })
      } else { // ÂÖ∂‰ªñÂºÇÂ∏∏Áõ¥Êé•ÊèêÁ§∫
        Message({
          showClose: true,
          message: '‚ö†' + res.message || 'Error',
          type: 'error',
          duration: 3 * 1000
        })
      }
      return Promise.reject(new Error(res.message || 'Error'))
    } else {
      return res
    }
  },
  error => {
    /** *** Êé•Êî∂Âà∞ÂºÇÂ∏∏ÂìçÂ∫îÁöÑÂ§ÑÁêÜÂºÄÂßã *****/
    // console.log('err' + error) // for debug
    Message({
      showClose: true,
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)
export default service